#FullMenu {
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    
    z-index: 1;

    display: none;

    ul {
        display: flex;
        flex-direction: column;
        gap: 2rem;

        li {
            text-align: center;

            a {
                font-size: 2rem;
                font-weight: bolder;
                color: var(--white);
            }
        }
    }

    .close {
        position: absolute;
        top: 3rem;
        right: 3rem;

        width: 40px;
        height: 40px;

        cursor: pointer;
    }
}

#FullMenu.on {
    display: flex;
}