// Footer
#Footer {
  margin-top: 4rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;

  width: 100%;
  padding: 3rem;
  background-color: var(--black);
  color: var(--white);

  ul {
    display: flex;
    gap: 2rem;
  }

  p {
    text-align: center;
  }
}

// Footer 반응형
@media only screen and (max-width: 500px) {
  #Footer {
    ul {
      flex-direction: column;
      align-items: center;
      gap: 0.5rem;
    }
  }
}
